/*----------------main-----------------*/
#content {
	background-color: var(--primary-bg);
}

.info {
	padding-top: 0;
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	justify-content: center;
	align-items: flex-start;
}

.info p:first-of-type {
	font-family: var(--font-family5);
	color: var(--font-color1);
	font-size: clamp(15px, 6vw, 16px);
	font-weight: 500;
	margin-bottom: 5px;
}

.info p:nth-child(2),
.info p:nth-child(3) {
	font-family: var(--font-family4);
	font-weight: 600;
	color: var(--font-color2);
	font-size: clamp(40px, 7vw, 80px);
}

.info p:nth-child(3) {
	color: var(--font-color6);
	margin-bottom: 20px;
	line-height: 1.3;
}

.info  p:last-of-type{
	font-family: 'Roboto', sans-serif;
	color: var(--font-color6);
	font-size: 16px;
	max-width: 550px;
	line-height: 1.5;
	margin-bottom: 60px;
	letter-spacing: 0.5px;
	text-align: left;
	font-weight: normal;
}

.info .cv-btn {
	cursor: pointer;
	display: inline-block;
}

.cv-btn a {
	background-color: transparent;
	display: inline-block;
	padding: 15px 50px;
	border: 1.5px solid #c46f00;
	border-radius: 5px;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	-ms-border-radius: 5px;
	-o-border-radius: 5px;
	font-family: var(--font-family5);
	color: var(--font-color1);
	font-size: 15px;
}

.cv-btn:hover > a{
	background-color: var(--font-color5);
}



.image-wrapper {
	max-width: 300px;
	max-height: 280px;
	position: relative;
	border-radius: 5px;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	-ms-border-radius: 5px;
	-o-border-radius: 5px;
}

.image-wrapper::after {
	position: absolute;
	content: '';
	border: 1.5px solid var(--font-color6);
	opacity: 0.5;
	width: 100%;
	height: 100%;
	top: 15px;
	left: 15px;
	border-radius: 5px;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	-ms-border-radius: 5px;
	-o-border-radius: 5px;
	transition: all .3s ease-in-out;
	-webkit-transition: all .3s ease-in-out;
	-moz-transition: all .3s ease-in-out;
	-ms-transition: all .3s ease-in-out;
	-o-transition: all .3s ease-in-out;
	z-index: 1;
}

.image-wrapper:hover::after {
	top: 10px;
	left: 10px;
	border-color: var(--font-color1);
	opacity: 1;
	transition: all .3s ease-in-out;
	-webkit-transition: all .3s ease-in-out;
	-moz-transition: all .3s ease-in-out;
	-ms-transition: all .3s ease-in-out;
	-o-transition: all .3s ease-in-out;

}

.wrapper .image {
	max-width: 300px;
	max-height: 280px;
	border-radius: 5px;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	-ms-border-radius: 5px;
	-o-border-radius: 5px;
	position: relative;
	overflow: hidden;
	z-index: 15;
}

.wrapper .image:hover img{
	filter: contrast(120%);
	transition: all .5s ease-in-out;
	-webkit-transition: all .5s ease-in-out;
	-moz-transition: all .5s ease-in-out;
	-ms-transition: all .5s ease-in-out;
	-o-transition: all .5s ease-in-out;
	-webkit-filter: contrast(120%);
}

.image img {
	width: 100%;
	height: 100%;
	vertical-align: middle;
	filter: grayscale(100%) contrast(1);
	-webkit-filter: grayscale(100%) contrast(1);
	transition: all .5s ease-in-out;
	-webkit-transition: all .5s ease-in-out;
	-moz-transition: all .5s ease-in-out;
	-ms-transition: all .5s ease-in-out;
	-o-transition: all .5s ease-in-out;
}


