.inner {
	max-width: 1280px;
	margin: 0 auto;
}

.main-inner {
	max-width: 1000px;
	margin: 0 auto;
} 

#portfolio {
	position: relative;
}

.swal2-title {
	font-size: 1rem !important;
}



/* Navbar */
.navbar {
	width: 100%;
	z-index: 9999;
	position: fixed;
	background-color: var(--primary-bg);
}


.nav-group {
	position: relative;
}

.menu {
	height: 60px;
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	align-items: center;
}

.logo-name {
	font-family: var(--font-family2);
	color: var(--font-color2);
	font-weight: lighter;
	font-size: 18px;
}

.nav {
	margin-left: auto;
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
}

.nav-item {
	margin: 0 15px;
	padding: 0 10px;
}

.nav-link {
	font-family: var(--font-family5);
	color: var(--font-color2);
	font-size: 15px;
}

.active {
	color: var(--font-color1);
}

.menu-btn {
	display: block;
	position: absolute;
	right: 0;
	top: 50%;
	transform: translateY(-50%);
	display: none;
}

.mobile-nav {
	display: none;
}