
/* About */
.my-info {
	padding: 100px 0;
}

.sec-title {
	font-family: var(--font-family4);
	font-size: clamp(25px , 4vw, 30px);
	color: var(--font-color2);
	font-weight: 500;
	margin-bottom: 50px;
	position: relative;
}

.sec-title::after {
	position: absolute;
	content: '';
	top: 50%;
	width: 20%;
	height: 0.5px;
	opacity: 0.3;
	background-color: var(--font-color6);
	margin-left: 10px;
}

.about {
	display: grid;
	grid-template-columns: 3fr 2fr;
	gap: 50px;
}

.about .content p{
	font-family: var(--font-family4);
	font-size: 16px;
	letter-spacing: 0.5px;
	margin-bottom: 15px;
	line-height: 1.4;
	text-align: left;
	font-weight: normal;
	color: var(--font-color6);
}

.skills {
	margin-top: 20px;
	display: grid;
	/* grid-template-columns: repeat(2, minmax(140px, 100px)); Original Code */
	grid-template-columns: repeat(2, 1fr); /*New Code*/
	gap: 0 10px;
	padding: 0;
}

.skills li {
	margin-bottom: 5px;
	font-size: 13px;
	padding-left: 20px;
	font-family: var(--font-family5);
	color: var(--font-color6);
	position: relative;
}

.skills li::before {
	position: absolute;
	content: '\f0da';
	font-family: FontAwesome;
	left: 0;
	color: var(--font-color1);
	font-weight: bold;
	line-height: 15px;
	font-size: 8px;
}

.image-wrapper {
	max-width: 300px;
	max-height: 280px;
	position: relative;
	border-radius: 5px;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	-ms-border-radius: 5px;
	-o-border-radius: 5px;
}

.image-wrapper::after {
	position: absolute;
	content: '';
	border: 1.5px solid var(--font-color6);
	opacity: 0.5;
	width: 100%;
	height: 100%;
	top: 15px;
	left: 15px;
	border-radius: 5px;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	-ms-border-radius: 5px;
	-o-border-radius: 5px;
	transition: all .3s ease-in-out;
	-webkit-transition: all .3s ease-in-out;
	-moz-transition: all .3s ease-in-out;
	-ms-transition: all .3s ease-in-out;
	-o-transition: all .3s ease-in-out;
	z-index: 1;
}

.image-wrapper:hover::after {
	top: 10px;
	left: 10px;
	border-color: var(--font-color1);
	opacity: 1;
	transition: all .3s ease-in-out;
	-webkit-transition: all .3s ease-in-out;
	-moz-transition: all .3s ease-in-out;
	-ms-transition: all .3s ease-in-out;
	-o-transition: all .3s ease-in-out;

}

.wrapper .image {
	max-width: 300px;
	max-height: 280px;
	border-radius: 5px;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	-ms-border-radius: 5px;
	-o-border-radius: 5px;
	position: relative;
	overflow: hidden;
	z-index: 15;
}

.wrapper .image:hover img{
	filter: contrast(120%);
	transition: all .5s ease-in-out;
	-webkit-transition: all .5s ease-in-out;
	-moz-transition: all .5s ease-in-out;
	-ms-transition: all .5s ease-in-out;
	-o-transition: all .5s ease-in-out;
	-webkit-filter: contrast(120%);
}

.image img {
	width: 100%;
	height: 100%;
	vertical-align: middle;
	filter: grayscale(100%) contrast(1);
	-webkit-filter: grayscale(100%) contrast(1);
	transition: all .5s ease-in-out;
	-webkit-transition: all .5s ease-in-out;
	-moz-transition: all .5s ease-in-out;
	-ms-transition: all .5s ease-in-out;
	-o-transition: all .5s ease-in-out;
}


