.error {
    position: fixed;
    top: 50%;
    left: 50%;
    width: 800px;
    height: 400px;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.error > h1, p {
    color: var(--font-color8);
    text-align: center;
    font-family: var(--font-family1);
    font-size: 18px;
    font-weight: bold;

}

.error > p:last-child {
    font-weight: normal;
}

.error > h1 {
    font-size: 85px;
}

.error .error-btn {
    display: block;
    width: 100px;
    height: 30px;
    margin-top: 50px;
    line-height: 30px;
    background-color: var(--font-color1);
    color: var(--font-color2);
    text-align: center;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
}