
/*----------------skills-----------------*/
.my-skill {
	padding: 100px 0;
	max-width: 900px;
	margin: 0 auto;
}

.skill-sec {
	/* display: grid;
	justify-content: start;
	grid-template-columns: 120px 1fr;
	gap: 30px; */
	display: flex;
}

.skill-tab {
	display: flex;
	flex-direction: column;
}

.tab-btn {
	padding: 0 20px 0;
	width: 100%;
	height: 42px;
	text-align: left;
	font-family: var(--font-family5);
	background-color: transparent;
	border: none;
	border-left: 2px solid #a8b2d12d;
	border-radius: 5px;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	-ms-border-radius: 5px;
	-o-border-radius: 5px;
	transition: all 0.1s linear;
	-webkit-transition: all 0.1s linear;
	-moz-transition: all 0.1s linear;
	-ms-transition: all 0.1s linear;
	-o-transition: all 0.1s linear;
}

.tab-btn span {
	color: var(--font-color6);
	font-family: var(--font-family5);
	font-size: 14px;
}

.tab-btn:hover {
	background-color: var(--font-color5);
}

.tab-btn:hover > span {
	color: var(--font-color1);
}

.highlight {
	background-color: var(--font-color5);
	border-color: #c46f00;
}

.highlight > span {
	color: #c46f00;
}

.tab-btn:focus {
	background-color: var(--font-color5);
	border-color: #c46f00;
}

.tab-btn:focus > span {
	color: #c46f00;
}







.skill-sec .description {
	width: 100%;
	height: auto;
	padding: 10px 5px;
	margin-left: 20px;
}


.description h3 {
	color: #c46f00;
	font-size: 22px;
	margin-bottom: 10px;
	font-family: var(--font-family4);
}

.description span {
	color: #a8b2d186;
	font-size: 14px;
	margin-bottom: 20px;
	display: block;
	font-family: var(--font-family5);
}

.description ul > li {
	font-family: var(--font-family4);
	font-size: 14px;
	color: #a8b2d1;
	line-height: 1.5;
	margin-bottom: 10px;
	letter-spacing: 0.5px;
	position: relative;
	padding-left: 15px;
}

.description ul > li::before {
	position: absolute;
	content: '\f0da';
	font-family: FontAwesome;
	left: 0;
	color: var(--font-color1);
	font-size: 12px;
	line-height: 20px;
}

/* New code 2024-04-18 */
.description ul > li > .dev_year {
	color: var(--font-color8);
}


/*-----skills css------*/
.description .skill_title {
	margin-bottom: 25px;
}

.description ul.skill_list {
	display: grid;
	gap: 10px;
	/* grid-template-columns: repeat(2, minmax(200px, 200px)); Original Code*/
	grid-template-columns: repeat(3, 1fr); /* New Code*/
}

.description .skill_list li {
	margin-bottom: 0;
	font-size: 14px;
}


.tabSkill{
	display: none;
}

.tabSkill-active{
	display: block;
}




















