
/* Contact */
.my-contact {
	width: 100%;
	padding: 100px 0;
}

.contact-sec {
	display: grid;
	grid-template-columns: 2fr 3fr;
	gap: 50px;
}

.contact-info p {
	color: var(--font-color6);
	font-family: var(--font-family4);
	font-size: clamp(14px, 5vw, 16px);
	margin-bottom: 30px;
}

.contact-info .address{
	display: grid;
	grid-template-columns: 1fr;
}

.address li {
	margin-bottom: 8px;
	padding-left: 20px;
	position: relative;
	font-family: var(--font-family5);
	color: var(--font-color6);
	font-size: 13px;
}

.address li a {
	font-family: var(--font-family5);
	color: var(--font-color6);
	font-size: 13px;
}

.address li::before {
	position: absolute;
	content: '\f0da';
	font-family: FontAwesome;
	left: 0;
	color: var(--font-color1);
	font-weight: bold;
	line-height: 15px;
	font-size: 8px;
}

.form {
	display: flex;
	flex-direction: column;
}

.form input,
.form textarea {
	padding: 10px;
	margin-bottom: 20px;
	background-color: transparent;
	color: var(--font-color6);
	font-family: var(--font-family4);
	border: 1px solid var(--font-color6);
	border-radius: 5px;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	-ms-border-radius: 5px;
	-o-border-radius: 5px;
	resize: none;
	outline: none;
	caret-color: var(--font-color6);
}

.form input::placeholder {
	color: rgb(93, 95, 95);
}

.form input:focus,
.form textarea:focus {
	background-color: transparent;
	box-shadow: 0 0 3px rgba(0, 156, 247, 0.8);
}

.form p {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
}

.form p input:first-of-type {
	flex: 1;
	margin-right: 10px;
}

.form p input:last-of-type {
	flex: 1;
}

.form-btn {
	margin-top: 20px;
	text-align: right;
}

.form-btn > input {
	padding: 10px 20px;
	border-radius: 5px;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	-ms-border-radius: 5px;
	-o-border-radius: 5px;
	cursor: pointer;
	background-color: transparent;
	border: 1px solid var(--font-color6);
	color: var(--font-color6);
}

.form-btn > input:hover {
	opacity: 0.7;
}
